.searchbar-results {
  .loading-wrapper {
    min-height: 51px !important;
    background: #271832 !important; } }

.loading-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  width: 100%;
  display: flex;
  height: 100%;
  background: #00000075;
  .lds-dual-ring {
    margin: auto;
    &::after {
      content: "";
      display: block;
      width: 46px;
      height: 46px;
      margin: 1px;
      border-radius: 50%;
      border: 3px solid #fcf;
      border-color: #fcf transparent #fcf transparent;
      animation: lds-dual-ring 800ms ease-in-out infinite; } } }

@keyframes lds-dual-ring {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
